<template>
  <v-row class="fill-height">
    <v-col>
      <v-sheet height="64">
        <v-toolbar flat>
          <v-btn fab text small color="grey darken-2" @click="prev">
            <v-icon small>
              mdi-chevron-left
            </v-icon>
          </v-btn>
          <v-btn fab text small color="grey darken-2" @click="next">
            <v-icon small>
              mdi-chevron-right
            </v-icon>
          </v-btn>
          <v-toolbar-title v-if="$refs.calendar">
            {{ $refs.calendar.title }}
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <template v-if="$auth.check(['ADMIN', 'RESPONSIBLE'])">
            <v-btn class="mr-2 my-2" @click="create()" color="#007bff" dark>
              <v-icon left>mdi-calendar-check</v-icon>
              {{ $vuetify.lang.t("$vuetify.btn.add") }}
            </v-btn>
          </template>
          <!--          <v-btn-->
          <!--            :loading="loading.refresh"-->
          <!--            @click="refreshItems()"-->
          <!--            class="mr-2 my-2"-->
          <!--            depressed-->
          <!--            color="#f1f5fc"-->
          <!--          >-->
          <!--            <v-icon left color="#007bff">mdi-refresh</v-icon>-->
          <!--            {{ $vuetify.lang.t("$vuetify.btn.refresh") }}-->
          <!--          </v-btn>-->
          <v-menu bottom right>
            <template v-slot:activator="{ on, attrs }">
              <v-btn outlined color="grey darken-2" v-bind="attrs" v-on="on">
                <span>{{ typeToLabel[type] }}</span>
                <v-icon right>
                  mdi-menu-down
                </v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="type = 'day'">
                <v-list-item-title>Day</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'week'">
                <v-list-item-title>Week</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'month'">
                <v-list-item-title>Month</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = '4day'">
                <v-list-item-title>4 days</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-toolbar>
      </v-sheet>
      <v-sheet height="600">
        <v-calendar
          ref="calendar"
          v-model="focus"
          color="primary"
          :events="events"
          :type="type"
          :loading="loading.list"
          @click:event="editAppointment"
          @click:more="viewDay"
          @click:date="viewDay"
        ></v-calendar>
      </v-sheet>
    </v-col>
    <form-appointment
      :dialog="dialogForm"
      :form="form"
      :messages="messages"
      @getItems="getItems"
      ref="appointmentForm"
    ></form-appointment>
    <delete-item
      :dialog="dialogDelete"
      :messages="messagesDelete"
      :urlItems="urlDeleteItems"
      @getItems="getItems"
    ></delete-item>
  </v-row>
</template>
<script>
import ListMixin from "./../../mixins/Common/List.vue";
import FormAppointment from "./Form";
import DeleteItem from "@/components/Common/Delete";

export default {
  components: {
    FormAppointment,
    DeleteItem
  },
  mixins: [ListMixin],
  data: () => ({
    urlItems: "appointments",
    focus: "",
    type: "month",
    typeToLabel: {
      month: "Month",
      week: "Week",
      day: "Day",
      "4day": "4 Days"
    },
    messages: {
      title: "",
      submit: "",
      success: ""
    },
    messagesDelete: {
      success: "$vuetify.doctor.delete.success"
    },
    form: {
      id: null,
      code_internal: null,
      dateNaiss: null,
      dateService: null,
      intervention_days: null,
      position: null,
      address: null,
      speciality: null,
      phone: null,
      email: null,
      department: null
    },
    loading: {
      list: false,
      refresh: false,
      filter: false
    },
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: []
  }),
  mounted() {
    this.$refs.calendar.checkChange();
    this.getItems();
  },
  methods: {
    editAppointment(event) {
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.appointment.update.title",
        submit: "$vuetify.appointment.update.submit",
        success: "$vuetify.appointment.update.success"
      };
      this.$refs.appointmentForm.setForm(event.event);
    },
    create() {
      this.form.id = null;
      this.form.problem = null;
      this.form.patient = null;
      this.form.start_appointment_date = null;
      this.form.department = null;
      this.form.end_appointment_date = null;
      this.form.postpone = false;
      this.form.doctor = null;
      this.dialogForm.show = true;
      this.messages = {
        title: "$vuetify.appointment.new.title",
        submit: "$vuetify.appointment.new.submit",
        success: "$vuetify.appointment.new.success"
      };
    },
    refreshItems() {
      let self = this;
      this.loading.refresh = true;
      this.getItems()
        .then(() => {})
        .catch(() => {})
        .then(() => {
          self.loading.refresh = false;
        });
    },
    getItems() {
      let self = this;
      const { page, itemsPerPage } = this.pagination;
      let params = {
        page: page,
        size: itemsPerPage
      };
      if (!this._.isEmpty(this._search)) {
        this._search = this._.merge(params, this._search);
      }
      return new Promise((resolve, reject) => {
        this.loading.list = true;
        self.$store
          .dispatch("request", {
            url: self.urlItems,
            params: params
          })
          .then(response => {
            this.events = [];
            let data = response.data;
            self.items = data.content;
            const events = response.data.content;
            for (const item of events) {
              const department = item.department;
              const problem = item.problem;
              const start_appointment_date = item.start_appointment_date;
              const end_appointment_date = item.end_appointment_date;
              const patient = item.patient;
              const doctor = item.doctor;
              const id = item.id;

              const start = item.start_appointment_date;
              const end = item.end_appointment_date;
              if (this.$auth.user().role === "ADMINISTRATOR") {
                const name = item.patient.name + " / " + item.doctor.name;
                this.events.push({
                  department,
                  problem,
                  start_appointment_date,
                  patient,
                  doctor,
                  end_appointment_date,
                  id,
                  name,
                  start,
                  end
                });
              } else if (this.$auth.user().role === "DOCTOR") {
                const name = item.patient.name;
                this.events.push({
                  department,
                  problem,
                  start_appointment_date,
                  patient,
                  doctor,
                  end_appointment_date,
                  id,
                  name,
                  start,
                  end
                });
              } else {
                const name = item.doctor.name;
                this.events.push({
                  department,
                  problem,
                  start_appointment_date,
                  patient,
                  doctor,
                  end_appointment_date,
                  id,
                  name,
                  start,
                  end
                });
              }
            }
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data.content[0]);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loading.list = false;
            resolve();
          });
      });
    },
    viewDay({ date }) {
      this.focus = date;
      this.type = "day";
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    }
  }
};
</script>
