<template>
  <v-dialog v-model="dialog.show" persistent max-width="880px">
    <v-card style="background-color: #ECEFF1">
      <v-card-title>
        <div class="title">
          {{ $vuetify.lang.t(messages.title) }}
        </div>
      </v-card-title>
      <v-form @submit.prevent="save">
        <v-card max-width="820px" class="ml-6">
          <v-card-text>
            <v-container>
              <v-row wrap>
                <v-col cols="12" sm="6" class="mb-0 py-0">
                  <v-autocomplete
                    outlined
                    dense
                    v-model="form.patient"
                    :items="itemsPatient"
                    :loading="isLoadingPatient"
                    :search-input.sync="searchPatient"
                    color="#007bff"
                    item-text="name"
                    item-value="id"
                    :label="
                      $vuetify.lang.t('$vuetify.appointment.cols.patient.title')
                    "
                    class="font-weight-bold"
                    clearable
                  ></v-autocomplete>
                </v-col>
                <!--                <v-col cols="12" sm="6" class="mb-0 py-0">-->
                <!--                  <v-text-field-->
                <!--                    outlined-->
                <!--                    dense-->
                <!--                      v-model="form.department"-->
                <!--                    color="#007bff"-->
                <!--                    :label="-->
                <!--                      $vuetify.lang.t(-->
                <!--                        '$vuetify.appointment.cols.department.title'-->
                <!--                      )-->
                <!--                    "-->
                <!--                    prepend-inner-icon="mdi-card-text-outline mdi-dark mdi-18px"-->
                <!--                    class="font-weight-bold"-->
                <!--                  ></v-text-field>-->
                <!--                </v-col>-->
                <v-col cols="12" sm="4" class="mb-0 py-0">
                  <v-autocomplete
                    outlined
                    dense
                    v-model="form.doctor"
                    :items="itemsDoctor"
                    :loading="isLoadingDoctor"
                    :search-input.sync="searchDoctor"
                    color="#007bff"
                    item-text="name"
                    item-value="id"
                    :label="
                      $vuetify.lang.t('$vuetify.appointment.cols.doctor.title')
                    "
                    class="font-weight-bold"
                    clearable
                  ></v-autocomplete>
                </v-col>
                <div>
                  <v-text-field
                    class="font-weight-bold ml-3"
                    outlined
                    dense
                    v-model="form.start_appointment_date"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t(
                        '$vuetify.appointment.cols.start_appointment_date.title'
                      )
                    "
                    :readonly="form.postpone === false || form.id !== null"
                  >
                    <template v-slot:append>
                      <date-pickerstart
                        @getdatetime_start="getdatetime_start"
                      ></date-pickerstart>
                    </template>
                  </v-text-field>
                </div>
                <div>
                  <v-text-field
                    class="font-weight-bold ml-4"
                    outlined
                    dense
                    v-model="form.end_appointment_date"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t(
                        '$vuetify.appointment.cols.end_appointment_date.title'
                      )
                    "
                  >
                    <template v-slot:append>
                      <date-pickerend
                        @getdatetime_end="getdatetime_end"
                      ></date-pickerend>
                    </template>
                  </v-text-field>
                </div>
                <v-col cols="12" sm="12" class="mb-0 py-0">
                  <v-textarea
                    outlined
                    dense
                    v-model="form.problem"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t('$vuetify.appointment.cols.problem.title')
                    "
                    class="font-weight-bold"
                  ></v-textarea>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-divider class="mx-4" style="margin-top: -25px"></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="deleteItem()" v-if="form.id !== null" color="red">
              {{
                $vuetify.lang.t(
                  "$vuetify.appointment.cols.cancel_appointment.title"
                )
              }} </v-btn
            ><v-btn @click="dialog.show = false">
              {{ $vuetify.lang.t("$vuetify.btn.close") }}
            </v-btn>
            <v-btn color="#007bff" :loading="loading" dark type="submit">
              {{ $vuetify.lang.t(messages.submit) }}
            </v-btn>
          </v-card-actions>
        </v-card>
        <v-card-text> </v-card-text>
      </v-form>
    </v-card>
    <delete-item
      :dialog="dialogDelete"
      :messages="messagesDelete"
      :urlItems="urlDeleteItems"
      @getItems="getItems"
    ></delete-item>
  </v-dialog>
</template>

<script>
// import { existNameAct } from "../../helpers/Validator";
import DatePickerend from "./date-picker-end.vue";
import DatePickerstart from "./date-picker-start.vue";
import FormMixin from "./../../mixins/Common/Form.vue";
import DeleteItem from "./Cancel_appointment";
// import {mapGetters} from "vuex";
// import { required, maxLength } from "vuelidate/lib/validators";

export default {
  mixins: [FormMixin],
  props: {
    form: {
      type: Object,
      default: function() {
        return {
          id: null,
          problem: null,
          patient: null,
          start_appointment_date: null,
          end_appointment_date: null,
          doctor: null
        };
      }
    }
  },
  data: () => ({
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    messagesDelete: {
      success: "$vuetify.appointment.cols.cancel_appointment.success"
    },
    dialogDelete: {
      show: false
    },
    menu: false,
    end_appointment_date: false,
    start_appointment_date: false,
    time_start: false,
    time_end: false,
    urlItems: "appointments",
    urlPatients: "patients",
    urlDoctors: "doctors",
    urlDeleteItems: "",
    isLoadingPatient: false,
    isLoadingDoctor: false,
    searchDoctor: null,
    searchDepart: null,
    itemsPatient: [],
    itemsDoctor: []
  }),

  watch: {
    searchPatient(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
        .dispatch("request", {
          url: self.urlPatients,
          params: params
        })
        .then(response => {
          console.log("respp", response.data.content);
          self.itemsPatient = response.data.content;
        })
        .finally(() => (this.isLoading = false));
    },
    searchDoctor(value) {
      if (value === null) return;
      const params = { name: value };
      let self = this;
      this.isLoading = true;
      this.$store
        .dispatch("request", {
          url: self.urlDoctors,
          params: params
        })
        .then(response => {
          console.log("respp", response.data.content);
          self.itemsDoctor = response.data.content;
        })
        .finally(() => (this.isLoading = false));
    }
  },
  created() {},
  validations() {
    let validators = {
      form: {}
    };
    return validators;
  },
  methods: {
    getdatetime_start(val) {
      this.form.start_appointment_date = val;
    },
    getdatetime_end(val) {
      this.form.end_appointment_date = val;
      console.log(this.form.end_appointment_date);
    },
    deleteItem() {
      console.log(this.form.id);
      this.urlDeleteItems = this.urlItems + "/" + this.form.id;
      this.dialogDelete.show = true;
    },
    createD() {
      this.dialogForm.show = true;
      this.formData = {
        id: null,
        name: null,
        coefficient: null,
        number_account: null,
        quote_internal: null,
        quote_external: null
      };
    },
    // Fonction pour récuperer les types de roles dans la store

    // Fonction pour récuperer le contenu des champs du formulaire
    getDataForm() {
      let data = {
        problem: this.form.problem,
        patient: this.form.patient,
        doctor: this.form.doctor,
        start_appointment_date: this.form.start_appointment_date,
        end_appointment_date: this.form.end_appointment_date
      };
      return data;
    },
    setForm(item) {
      console.log(this.form.id);
      // Données envoyées lors de la modification
      this.form.id = item.id;
      this.form.problem = item.problem;
      this.form.start_appointment_date = item.start_appointment_date;
      this.form.end_appointment_date = item.end_appointment_date;
      this.form.patient = item.patient["id"];
      this.itemsPatient.push(item.patient);
      this.form.doctor = item.doctor["id"];
      this.itemsDoctor.push(item.doctor);
    }
  },
  computed: {
    // Gestion des erreurs du formulaire
  },
  components: {
    DatePickerend,
    DatePickerstart,
    DeleteItem
  }
};
</script>
